.Modal-overlay{
    position: fixed;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    background-color: rgba(0,0,0,0.16);
  }
  .Modal{
      position: fixed;
      z-index: 1;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      width: 500px;
      border-radius: 10px;
      background-color: white;
      box-shadow: 0px 3px 6px rgba(0,0,0,0.16);
      & > * {
        &:first-child{
          margin-top: 16px;
        }
        margin-left: 16px;
        margin-right: 16px;
      }
      p.title{
        font-size: 18pt;
        font-weight: bold;
        color: #333;
        text-align: center;
        
      }
      .content{
        border-top: 1px solid #bebebe;
        margin-top: 16px;
        
        p{
          padding: 8px;
          font-size: 12pt;
          color: #999;
          text-align: center
        }
      }
  
      .button-wrap{
        margin: 0;
        margin-top: 8px;
        button{
          width: 100%;
          padding: 12px 0;
          border-radius: 0 0 10px 10px;
          background-color: #8FB8FE;
          font-size: 13pt;
          color: white;
          border: 0;
          cursor: pointer;
          &:hover{
            background-color: #9E8FFE;
          }
          &:active{
            background-color: #9E8FFE;
          }
        }
      }
    }